<template>
  <header :class="{'header-container': isPc, 'header-container-mobile': !isPc}">
    <div v-if="isPc" class="head">
      <a class="home-text" @click="gotoHome()" :style="{backgroundImage: 'url(' + $assetsUrl + 'navigation-logo.svg)'}"></a>
      <div class="header-right">
        <!--<a href="https://whaleblue.zendesk.com">幫助中心</a>-->
        <a @click="gotoSupport()">幫助中心</a>
        <a-button class="subscribe-btn" type="primary" ghost @click="gotoOrder()">現在訂閱</a-button>
        <a v-if="!isLogin" @click="goToService">登錄後台</a>
        <a v-if="isLogin" class="language" @click="goToService">
          用戶後台
          <i class="anticon anticon-down"></i>
          <div class="lang-popup">
            <div class="lang-arrow" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/switch-arrow-lang.svg)'}"></div>
            <div class="lang-item" @click.stop="changePassword">
              <a >修改密码</a>
            </div>
            <div class="lang-item" @click.stop="logOut">
              <a >登出</a>
            </div>
          </div>
        </a>
        <a class="language">
          {{lang}}
          <i class="anticon anticon-down"></i>
          <div class="lang-popup">
            <div class="lang-arrow" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/switch-arrow-lang.svg)'}"></div>
            <!--<div class="lang-item" v-on:click="changeLang('en-US')">-->
            <!--<a >English</a>-->
            <!--</div>-->
            <div class="lang-item" v-on:click="changeLang('zh-TW')">
              <a >繁體中文</a>
            </div>
            <!--<div class="lang-item" v-on:click="changeLang('zh-CN')">-->
            <!--<a >简体中文</a>-->
            <!--</div>-->
          </div>
        </a>
      </div>
    </div>
    <div v-if="!isPc" class="header-tag">
      <a class="home-text" @click="gotoHome()"></a>
      <button class="hamburger hamburger--squeeze" type="button" @click="openMenu" :class="{'is-active': isOpen}">
        <span class="hamburger-box">
          <span class="hamburger-inner"></span>
        </span>
      </button>
    </div>
    <!--<i v-if="!isPc" class="head-menu" @click="openMenu"></i>-->
    <div class="menu-content" id="menu-content" v-if="!isPc" :style="{display: isOpen?'flex': 'none'}">
      <a class="header-link" @click="gotoSupport()">幫助中心</a>
      <a-button class="subscribe-btn" type="primary" ghost @click="gotoOrder()">現在訂閱</a-button>
      <a class="header-link" :class="'a-top-line'" @click="goToService">{{!isLogin ? '登錄後台' : '用戶後台'}}</a>
      <a class="header-link" v-if="isLogin" @click="logOut">登出</a>
      <!--<a-button class="subscribe-btn" type="primary" ghost @click="gotoOrder()">現在訂閱</a-button>-->
      <a-radio-group class="lang-switch" @change="mbChangLang" v-model="langMb" >
        <a-radio :value="1" >繁體中文</a-radio>

      </a-radio-group>
    </div>
  </header>
</template>

<script>
  /* eslint-disable */
  export default {
    name: 'PublicHeader',
    data() {
      return {
        lang: 'English',
        isPc: this.$isPc(),
        isNotInApp: !!localStorage.getItem('inapp'),
        isLogin: false,
        isOpen: false,
        langMb: 1,
        backgroundURL: `${window.location.origin}/my/service`
      };
    },
    props: {
      isLogout: false
    },
    created() {
      this.lang = this.getLang(window.localStorage.getItem('localeLanguage'));
    },
    mounted() {
      this.isLogin = !!localStorage.getItem('token');
    },
    methods: {
      mbChangLang() {

      },
      goToService() {
        if (this.isLogin) {
           this.$router.push({
             path: 'my/service',
             name: 'service'
           });
        } else {
          this.$router.push({
            path: 'users/signin',
            name: 'signin'
          });
        }
        if (!this.isPc) {
          this.openMenu()
        }
      },
      changePassword() {
        this.$router.push({
          path: 'users/signin',
          name: 'change-password'
        });
      },
      logOut() {
        this.$emit("isLogout", true);
        localStorage.removeItem('token');
        localStorage.removeItem('email');
        this.isLogin = !!localStorage.getItem('token');
        if (this.$router.currentRoute.name === 'service') {
          this.$router.push({
            path: 'home',
            name: 'home'
          });
        }
      },
      changeLang(lang) {
        this.lang = this.getLang(lang);
        this.$setupLang(lang);
      },
      getLang(lang) {
        switch (lang) {
          // case 'en-US':
          //   return 'English'
          //   break;
          case 'zh-TW':
            return '繁體中文';
            break;
          // case 'zh-CN':
          //   return '简体中文'
          //   break;
          default:
            return '繁體中文';
        }
      },
      gotoOrder() {
        this.$router.push({
          path: 'order/place-order',
          name: 'pay-order'
        });
      },
      gotoHome() {
        this.$router.push({
          path: '',
          name: 'home'
        });
      },
      gotoSupport() {
        if (window.location.pathname === "/zh-TW/help-center") {

        } else if (window.location.pathname.indexOf("/zh-TW/help-center") > -1){
          this.$router.push({
            path: 'help-center',
            name: 'support'
          });
        } else {
          window.open(`${window.location.origin}/help-center`, '_blank')
        }
      },
      openMenu() {
        if (this.isOpen) {
          const that = this;
          TweenMax.fromTo(
            document.getElementById('menu-content'),
            0.3,
            {right: '0'},
            {css:{right: '-100%'}, ease: Power0.easeNone, onComplete: function () {
                that.isOpen = false
              }}
          )
        } else {
          this.isOpen = true
          TweenMax.fromTo(
            document.getElementById('menu-content'),
            0.3,
            {right: '-100%'},
            {right: '0', ease: Power0.easeNone}
          )
        }

      }
    }
  };
</script>
